<template>
    <div class="stuworklist">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 课程设计
                </el-breadcrumb-item>
                <el-breadcrumb-item>{{items.name}}</el-breadcrumb-item>
            </el-breadcrumb> 
        </div>
        <div class="container" v-loading="loading">
            <div class="form">
                <h3>{{items.name}}</h3>
                <el-form label-width="110px">
                    <el-form-item class="work-date" size="small" label="作业截止日期：">
                        {{items.deadlineTime}}
                    </el-form-item>
                    <el-form-item label="教师提示：">
                        <div class="teacherishi">{{items.teacherReminder}}</div>
                    </el-form-item>
                    <el-form-item label="学生作业：">
                        <el-table
                            class="stu-list" 
                            size="mini"
                            height="330"
                            :data="items.workForTeacherDTOList"
                            style="width: 100%">
                            <el-table-column
                            type="index"
                              label="序号">
                            </el-table-column>
                            <el-table-column
                              label="姓名">
                              <template slot-scope="scope">
                                <span>{{ scope.row.studentName }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="入学年份">
                              <template slot-scope="scope">
                                <span>{{ scope.row.enrollmentYear }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="学号">
                              <template slot-scope="scope">
                                <span>{{ scope.row.studentNumber }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="院系">
                              <template slot-scope="scope">
                                <span>{{ scope.row.academyName }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="班级">
                              <template slot-scope="scope">
                                <span>{{ scope.row.className }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="作业项目">
                              <template slot-scope="scope">
                                <span>{{scope.row.workProjectName}}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="作业状态">
                              <template slot-scope="scope">
                                <span>{{scope.row.statusDescribe}}</span>
                              </template>
                            </el-table-column>
                            <el-table-column label="操作" width="80px">
                              <template slot-scope="scope">
                                <el-button type="text" @click="viewwork(scope.row.id)">{{scope.row.status==0||scope.row.status==3?'查看作业':'批阅作业'}}</el-button>
                              </template>
                            </el-table-column>
                          </el-table>
                    </el-form-item>
                    <el-form-item label="讨论区：">
                        <el-tabs type="border-card">
                          <el-tab-pane label="公共讨论区">
                            <talkCmp @getmore="getmore2" @pus="Addmessagebystudent2" @dele="deletemessage2" :workMessageInfoDTOList="workMessageInfoDTOList2" :total="totalsi2"/>
                          </el-tab-pane>
                        </el-tabs>
                    </el-form-item>
                </el-form>
                
            </div>
        </div>
    </div>
</template>

<script>
import {fetchGetteachingbyId, fetchTeacherlistbyteachindid, fetchAddteachermessage, fetchtDeleteteachermessage} from '../../axios/requests'
import talkCmp from '../common/talkCmp.vue'
export default {
    name: 'course',
    components:{talkCmp},
    data() {
        return {
            id:'',
            loading:false,
            items:'',

            //讨论消息
          startId2:0,
          workMessageInfoDTOList2:[],
          totalsi2:0,
        };
    },
    mounted(){
      let {pid} = this.$route.params;
      this.id = pid;
      this.GetteachingbyId();
      this.Studentlistmessage2(1)
    },
    methods: {

      async Addmessagebystudent2(talktext) {//添加讨论消息
        let {id} = this;
        if (!talktext) {
          this.$message({
            showClose: true,
            message: '讨论内容不能为空',
            type: 'error'
          });
          return;
        };
        this.loading = true;
        try {
          let response = await fetchAddteachermessage(id, talktext);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '发表成功',
              type: 'success'
            });
            this.talktext = '';
            this.Studentlistmessage2(1)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      getmore2(pagenum){
        this.Studentlistmessage2(pagenum)
      },
      async Studentlistmessage2(pageNum) {//得到讨论消息
        var size=10;
        if (pageNum==1) {
          this.workMessageInfoDTOList2=[];
          this.startId=0;
          var size=5;
        };
        try {
          let response = await fetchTeacherlistbyteachindid(this.id, pageNum, 5, this.startId2);
          if (response.status.code ==0) {
            this.totalsi2 = response.result.teachingMessageInfoDTOBaseListDTO.total;
             this.workMessageInfoDTOList2=response.result.teachingMessageInfoDTOBaseListDTO.list.reverse().concat(this.workMessageInfoDTOList2)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
      deletemessage2(e){
        this.$confirm('确定要删除吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Deletestudentmessage2(e.id, e.index);
          })
          .catch(() => {});
      },
      async Deletestudentmessage2(id, index) {//删除讨论消息
        this.loading = true;
        try {
          let response = await fetchtDeleteteachermessage(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            });
            this.workMessageInfoDTOList2.splice(index, 1);
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },

      viewwork(id){
        this.$openBlank('Sworkdetail',{id});
      },
      async GetteachingbyId() {//得到教学详情
        this.loading = true;
        let {id} = this;
        try {
          let response = await fetchGetteachingbyId(id);
          if (response.status.code ==0) {
            this.items = response.result;
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
    }
};
</script>

<style lang='scss'>

</style>

<style lang='scss' scoped>
    .stuworklist{
      .crumbs{
          padding-left:20px;
        }
        .form{
          color:#444;
          .work-date{
            margin-bottom:0;
          }
          h3{
            font-size:16px;
            margin-bottom:15px;
          }
          .teacherishi{
            line-height:22px;
            padding-top:9px;
          }
        }
        .stu-list{
            border:1px solid #ccc;
        }
    }
</style>


